.addToCartButton {
    display: flex;
    align-items: center;
    font-family: 'Playfair Display', sans-serif;
    font-size: 24px;
  }
  
  .quantityControl {
    display: flex;
    border: 1px solid lightgray;
    border-radius: 4px;
  }
  
  .quantityButton {
    background-color: transparent;
    border: none;
    padding: 4px 8px;
    cursor: pointer;
  }
  
  .quantityInput {
    border: none;
    text-align: center;
    width: 40px;
    padding: 4px;
    -moz-appearance: textfield;
    appearance: textfield;
  }
  
  .quantityInput::-webkit-inner-spin-button,
  .quantityInput::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  .addToCartButton{
    display: flex;
    align-items: end;
    gap: 1vw;
  }
  .addToCartButton button {
    background-color: black;
    color: white;
    border: none;
    border-radius: 0;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .addToCartButton button:hover {
    background-color: #333;
  }
  
  @media (max-width: 1150px) {
    .addToCartButton {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  .amount{
    margin: 0 5px 0 0;
  }
  .quantityControlWithAmount{
    display: flex;
    flex-wrap: wrap;
  }
  